import React, { useState } from 'react';
import logo from './logo.svg';
import './App.css';
import { Button, Empty, InputNumber, Space } from 'antd';
import { CheckOutlined, CloseOutlined, MinusOutlined, PlusOutlined } from '@ant-design/icons';

interface Problem {
  key: number,
  num1: number,
  num2: number,
  type: string,
  _result: number,
  result?: number|undefined,
}
const createRandomNumber = (max: number)=>{
  var randomNumber = Math.floor(Math.random() * max)
  return randomNumber
}
/**
 * @description 文字转语音方法
 * @public
 * @param { text, rate, lang, volume, pitch } object
 * @param  text 要合成的文字内容，字符串
 * @param  rate 读取文字的语速 0.1~10  正常1
 * @param  lang 读取文字时的语言
 * @param  volume  读取时声音的音量 0~1  正常1
 * @param  pitch  读取时声音的音高 0~2  正常1
 * @returns SpeechSynthesisUtterance
 */
 function speak({ text, speechRate, lang, volume, pitch }: any, endEvent: any, startEvent: any) {
  if (!window.SpeechSynthesisUtterance) {
      console.warn('当前浏览器不支持文字转语音服务')
      return;
  }

  if (!text) {
      return;
  }

  const speechUtterance = new SpeechSynthesisUtterance();
  speechUtterance.text = text;
  speechUtterance.rate = speechRate || 1;
  speechUtterance.lang = lang || 'zh-CN';
  speechUtterance.volume = volume || 1;
  speechUtterance.pitch = pitch || 1;
  speechUtterance.onend = function() {
      endEvent && endEvent();
  };
  speechUtterance.onstart = function() {
      startEvent && startEvent();
  };
  speechSynthesis.speak(speechUtterance);
  
  return speechUtterance;
}
function App() {
  const [maxValue, setmaxValue] = useState(20)
  const [problemCount, setproblemCount] = useState(25)
  const [problems, setProblems] = useState<Problem[]>([])
  const getProblems = (total: number)=>{
    const list: Problem[] = []
  
    for (let i = 0; i < total; i++) {
      const num1 =  createRandomNumber(maxValue)
      const num2 =  createRandomNumber(maxValue)
      const type =  num1>= num2 ? '-' : '+'
      const _result =  type==='+' ? num1+num2 : num1-num2
      const item: Problem = {
        key: i,
        num1,
        num2,
        type,
        _result,
        result: undefined
      }
      list.push(item)
    }
    return list
  }
  const createProblems = ()=>{
    setProblems(getProblems(problemCount))
  }
  const statistics = (list: any[])=>{
    const rightProblems = list.filter(({_result, result}: any) => _result === result);
    const rate = (rightProblems.length/list.length * 100).toFixed(1)
    alert(`成绩：${rate}分`)
  }
  const done = ()=>{
    const list = [...problems]
    setProblems(list)
    statistics(list)
  }
  const getSuffix = ({_result, result}: Problem)=>{
    if(result===null || result===undefined){
      return ''
    }
    if(_result === result){
      return <span style={{color: 'blue'}}><CheckOutlined /></span>
    }else{
      return <span style={{color: 'red'}}><CloseOutlined /></span>
    }
  }
  return (
    <div className="App">
      <header>
        <Space size={10}>
          几以内的算数：
          <InputNumber
            type={'number'}
            controls={false} 
            style={{width: '80px'}} 
            min={0} 
            max={1000} 
            defaultValue={maxValue} 
            onChange={(value: any)=>{
              setmaxValue(value)
            }}
          />
          多少道题目：
          <InputNumber
            type={'number'}
            controls={false} 
            style={{width: '80px'}} 
            min={0} 
            max={100} 
            defaultValue={problemCount} 
            onChange={(value: any)=>{
              setproblemCount(value)
            }}
          />
          <Button type="primary" onClick={createProblems}>生成题目</Button>
        </Space>
      </header>
      <div className='problems'>
        {
          problems.length ? problems.map((problem: Problem, index: number)=> {
            const {num1, num2, type, _result, result, key} = problem
            return <div className='problem' key={key}>
              <Space size={10}>
                <span style={{width: '30px', display: 'inline-block'}}>{index+1}</span>
                <InputNumber controls={false} min={0} max={maxValue} value={num1} style={{width: '60px'}}/>
                <span>{type==='+'? <PlusOutlined /> : <MinusOutlined />}</span>
                <InputNumber controls={false} min={0} max={maxValue} value={num2} style={{width: '60px'}} />
                <span style={{fontSize: '28px'}}>=</span>
                <InputNumber
                  type={'number'}
                  controls={false} 
                  style={{width: '120px'}} 
                  suffix={getSuffix(problem)} 
                  min={0} 
                  max={maxValue*2} 
                  defaultValue={result} 
                  onChange={(value: any)=>{
                    problem.result = value
                    console.log(problem)
                  }}
                  // onBlur={(e)=>{
                  //   const text = _result.toString() === e.target.value ? `嘟嘟真棒！`:'抱歉，答错了！'
                  //   speak({ text }, function() {
                  //       console.log('语音播放结束');
                  //   }, function() {
                  //       console.log('语音开始播放');
                  //   });
                  // }}
                />
              </Space>
            </div>
          } ) : <Empty />
        }
      </div>
      <footer>
        <Button type="primary" onClick={done} block>完成考试</Button>
      </footer>
    </div>
  );
}

export default App;
